@import 'variables';

.main-sections-paragraph{
	text-transform: uppercase;
	font-weight: bold;
	padding: 0.3em 0.2em !important;
	font-size: $font-size-h2;
	color: $color-text-footer;	
}



.main-sections{
	position: relative;
	width: 90%;
	height: 44px;
	margin: 20px 0 20px 0 ;
}
.red-line{
	color: $color-menu-hover-active;
	background: $color-menu-hover-active;
}
.black-line{
	color: $color-bacground-footer;
	background: $color-bacground-footer;
}
.brown-line{
	color: $color-brown;
	background: $color-brown;
}

.green-line{
	color: $color-green;
	background: $color-green;
}


.left{
	position: relative;
	left: 0;
	margin-right: 0;

}
.right{
	position: relative;
	right: -10%;
	margin-left: 0;
}